@font-face {
  font-family: "PTSans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "PTSans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}
body {
  margin: 0;
  padding: 0;
  font-family: "PTSans", "Arial", sans-serif;
  text-transform: uppercase;
  background-color: #ffffff;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.margin-center {
  margin-left: auto;
  margin-right: auto;
}
.list-style {
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu-toggle {
  position: relative;
  display: block;
  width: 63px;
  height: 56px;
  margin-left: auto;
  background-color: #ffffff;
  border: none;
}
.menu-toggle__icon {
  fill: #81b3d2;
}
.menu-toggle__icon--cross {
  position: absolute;
  content: "";
  right: 2px;
  top: -227px;
  fill: #ffffff;
  border: 18px solid transparent;
}
.menu-toggle__text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .menu-toggle {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (max-width: 767px) {
  .main-nav {
    display: none;
  }
  .main-nav--opened {
    display: block;
  }
  .main-nav--closed {
    display: none;
  }
  .main-nav--nojs {
    display: block;
  }
}
.logo {
  position: absolute;
  left: 50%;
  display: block;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .logo {
    top: -56px;
  }
}
@media (min-width: 1200px) {
