  padding-right: 0;
  padding-bottom: 11px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 800;
  text-transform: none;
  text-align: left;
}
.form__item-label--radio::before {
  position: absolute;
  content: "";
  top: 36px;
  left: 0;
  width: 22px;
  height: 22px;
  background-color: #f2f2f2;
  border: 17px solid #f2f2f2;
