  background-color: #f2f2f2;
  border: 17px solid #f2f2f2;
  border-radius: 10%;
  transform: translateY(-50%);
}
.form__item-input {
  flex-grow: 1;
  min-width: 1%;
  width: 100%;
  padding-top: 12px;
  padding-left: 19px;
  padding-bottom: 12px;
  box-sizing: border-box;
}
.form__item-input--contacts {
  width: 100%;
  padding-right: 58px;
}
.form__item-radio,
.form__item-checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.form__item-radio:checked + .form__item-label--radio::before {
  background-color: #333333;
}
.form__item-radio:hover:checked + .form__item-label--radio::before {
  background-color: #333333;
  border: 17px solid #e5e5e5;
}
.form__item-radio:active:checked + .form__item-label--radio::before {
  background-color: #a4a4a4;
  border: 17px solid #d5d5d5;
}
.form__item-radio:hover + .form__item-label--radio::before,
.form__item-checkbox:hover + .form__item-label--checkbox::before,
.form__item-checkbox:checked:hover + .form__item-label--checkbox::before {
  background-color: #e5e5e5;
  border: 17px solid #e5e5e5;
}
.form__item-radio:active + .form__item-label--radio::before,
.form__item-checkbox:active + .form__item-label--checkbox::before {
  background-color: #d5d5d5;
  border: 17px solid #d5d5d5;
}
.form__item-checkbox:checked + .form__item-label--checkbox::before {
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  background-color: #f2f2f2;
  border: 17px solid #f2f2f2;
}
.form__item-checkbox:active:checked + .form__item-label--checkbox::before {
  background-color: #7c7c7c;
  border: 17px solid #7c7c7c;
  opacity: 0.3;
}
.form__item-textarea {
