  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button--disabled,
.button:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
  cursor: unset;
}
.button--disabled:hover,
.button:disabled:hover,
.button--disabled:active,
.button:disabled:active {
  color: #ffffff;
  background-color: #e5e5e5;
}
.page-wrapper {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .page-wrapper {
    min-width: 768px;
  }
}
@media (min-width: 1200px) {
  .page-wrapper {
    max-width: 1200px;
  }
}
